@import '../styles/_variables.scss';

html{
  height: 100%;
  width: 100%;
  position: relative;
  background-color: black;
  display: flex;
  justify-content: center;
}

body {
  position: relative;
  color: white;
  background:white;
  width: $max-width;
}

p{
  margin: 0;
}

#root{
  max-width: $max-width;
  height: 100%;
}

::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

.background-image{
  height: 100%;
  width: 100%;
  background-size: cover;
  background-repeat:no-repeat;
  background-position: center center;
}

.safearea{
  padding-top:env(safe-area-inset-top);
  /* padding-bottom:env(safe-area-inset-bottom); */
}

.vertical-middle-line{
  width: 1px;
  background-color: black;
  height: 100%;
  position: absolute;
  left:50%;
  top:0;
  z-index: 9999;
}

.vertical-middle-line p{
  color: red;
  position: absolute;
  top: 50%;
  display: flex;
  flex-wrap: nowrap;
}

.horizontal-middle-line{
  width: 100%;
  background-color: black;
  height: 1px;
  position: absolute;
  top:50%;
  left:0;
  z-index: 9999;
}
