@import './variables';
@import './common';


.background-overlay{
    position: absolute;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.6);
}


.button-container{
    margin-top: 10px;
    display: flex;
    justify-content: center;
    button{
        width: 300px;
    }
}


.home{
    box-sizing: border-box;
    

    .header-image{
        margin-top: 20px;
        max-width: 80%;
    }

    .app-description{
        max-width: 80%;
        margin-top: 30px;
        margin-bottom: 30px;
        text-align: center;
        font-family: 'CrimsonPro';
        color: $default_gray;
        font-style: italic;
        font-size: 16px;
    }

    .bubble-container{
        display: flex;
        width: 100%;
        justify-content: flex-start;
        width: 300px;
        margin-top: 30px;
        flex-direction: column;
        
        .item{
            display: flex;
            align-items: center;
            color: $default_orange;
            text-transform: uppercase;
            font-size: calc(20px * $font-size-increase-factor);
            font-family: 'Oswald-Bold';
            letter-spacing: 1px;
            text-decoration: underline 1px;
            text-decoration-line: underline 1px;
            text-underline-offset: 4px;
            -webkit-text-decoration-line: underline 1px red;
            -webkit-text-underline-offset: 4px;
            margin-bottom: 20px;
            cursor: pointer;
        
            img{
                width: 70px;
                height: 70px;
                margin-right: 20px;
                border-radius: 50%;
                border: solid $default_orange 1px;
            }
        }
    }
}
.open-close-container{
    box-sizing: border-box;
    display: flex;
    width: 100%;
    justify-content: flex-end;
    padding: 20px;
    min-height: 64px;

    .open-close-button{
        cursor: pointer;
        color: white;
    }
}

.modal-menu{
    
  box-sizing: border-box;
    position: absolute;
    max-width: 100%;
    height: 100%;
    overflow-y: auto;
    background-color: rgba(0, 0, 0, 0.95);
    top: 0;
    left: 0;
    overflow-y: auto;


    .image-container{
        margin-top: -30px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        img{
            width: 80%;
        }
    }

    .select-container{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 20px;
    }

    .full-line{
        width: 100%;
        height: 2px;
        background-color: $default_orange;
        margin-top: 30px;
        margin-bottom: 30px;
    }

    .menu-items{
        display: flex;
        width: 100%;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        .item{
            width: 300px;
            color: $default_orange;
            font-family: 'Oswald-Regular';
            text-transform: uppercase;
            text-decoration: underline;
            font-size: calc( 20px * $font-size-increase-factor);
            margin-top: 20px;
            cursor: pointer;
        }
    }

}