@import '_common.scss';
@import '_variables.scss';

.web-navigation{
    position: absolute;
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 10%;
    top: 0;
    left: 0;

    .left,.right{
        background-color: transparent;
        width: 100px;
        height: 100%;
        transition: all .2s;

        &:active{
            background-color: black;
            opacity: 0.7;
        }
    }
}

.chapter-page-content{
    height: 100%;
}

.chapter-page {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: black;
    overflow-x: hidden;

    div{
        pointer-events: none;
    }

    .need-pointer-event{
        pointer-events: all !important;
    }

    .chapter-container {
        pointer-events: all !important;
        max-width: 645px;
        height: calc(100% - 70px - (env(safe-area-inset-bottom) / 2));
        flex-grow: 1;
        overflow-y: auto;
        margin-left: auto;
        margin-right: auto;
    }

    .content-table-menu{
        pointer-events: all !important;
        max-width: 645px;
        margin-left: auto;
        margin-right: auto;
        height: calc(70px + (env(safe-area-inset-bottom) / 2));
        background-color: $default-orange;
        position: relative;

        div{
            pointer-events: all !important;
        }
    }
}