@import './variables.scss';
@import './common';

.inventory-page{
    display: flex;
    flex-direction: column;
    background-image: url('../../public/upload/images/64ed011e17b7622c4d323360/Storybook/Storybook_AlexInventory1.png');
    background-size: cover;
    background-repeat:no-repeat;
    background-position: center center;
    
    background-color: black;
    min-height: 100%;
    width: 100%;

    padding-top:env(safe-area-inset-top);
    padding-bottom:env(safe-area-inset-bottom);

    .content{
        display: flex;
        padding-top: 30%;
        flex-direction: column;
        min-height: 100%;
        background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0) 60%, rgba(0,0,0,0) 100%);
        align-items: center;
        text-transform: uppercase;

        .line{
            width: 80%;
            height: 2px;
            background: linear-gradient(90deg, rgba(187,138,67,0) 0%, rgba(187,138,67,1) 15%, rgba(187,138,67,1) 85%, rgba(187,138,67,0) 100%);
        }

        .top{
            img{
                position: absolute;
                left: calc(50% - 7px);
                width: 15px;
                margin-top: -17px;
            }
        }

        .bottom{
            img{
                position: absolute;
                left: calc(50% - 7px);
                width: 15px;
                margin-bottom: 2px;
            }
        }

        h1{
            font-size: 40px;
            font-family: 'Oswald-Regular';
            padding: 10px 0px;
        }

        .description{
            margin-top: 30px;
            font-family: 'CrimsoPro';
            font-size: 15px;
            max-width: 80%;
            text-align: center;
            text-transform: none;
        }

        .inventory-list-container{
            width: 100%;
            margin-bottom: 30px;
            
            .item{
                display: flex;
                width: 100%;
            }

            p{
                padding-left: 10%;
                padding-right: 10%;
            }

            .chapter-title{  
                font-size: 20px;
                color: $default_orange;
                font-family: 'Oswald-Regular';
                text-align: left;
                padding: 20px 0px;
                margin-top: 10px;
                border-top: $default_orange 2px solid;
                border-bottom: $default_orange 2px solid;
                background: linear-gradient(90deg, rgba(187,138,67,0.4) 0%, rgba(187,138,67,0) 70%);
            }

            .chapter-item{
                font-family: "CrimsonPro";
                font-size: 15px;
                justify-content: space-between;
                padding: 10px 0px 5px 0px;

                p:last-child{
                    text-wrap: nowrap;
                }
            }

            .white-gradient-line{
                width: 100%;
                height: 2px;
                margin: 0px 20px;
                background: linear-gradient(90deg, rgba(255,255,255,0) 0%, rgba(255,255,255,0.6469817546583851) 7%, rgba(255,255,255,0.8457395186335404) 28%, rgba(255,255,255,0.429590450310559) 57%, rgba(255,255,255,0) 95%);
            }

        }
    }
}